/*--------------------------------------------------------------
# Embedded Fonts
--------------------------------------------------------------*/

/**
 * Sample @font-face rules for Merriweather
 * 
*/

@font-face {
    font-family: 'Merriweather Web';
    src: url('../assets/fonts/Merriweather/fonts/merriweather-light-webfont.eot');
    src: url('../assets/fonts/Merriweather/fonts/merriweather-light-webfont.eot?#iefix') format('embedded-opentype'),
        url('../assets/fonts/Merriweather/fonts/merriweather-light-webfont.woff2') format('woff2'),
        url('../assets/fonts/Merriweather/fonts/merriweather-light-webfont.woff') format('woff'),
        url('../assets/fonts/Merriweather/fonts/merriweather-light-webfont.ttf') format('truetype'),
        url('../assets/fonts/Merriweather/fonts/merriweather-light-webfont.svg#merriweatherlight') format('svg');
    font-weight: normal;
    font-style: normal;
}

@font-face {
    font-family: 'Merriweather Web';
    src: url('../assets/fonts/Merriweather/fonts/merriweather-lightitalic-webfont.eot');
    src: url('../assets/fonts/Merriweather/fonts/merriweather-lightitalic-webfont.eot?#iefix') format('embedded-opentype'),
        url('../assets/fonts/Merriweather/fonts/merriweather-lightitalic-webfont.woff2') format('woff2'),
        url('../assets/fonts/Merriweather/fonts/merriweather-lightitalic-webfont.woff') format('woff'),
        url('../assets/fonts/Merriweather/fonts/merriweather-lightitalic-webfont.ttf') format('truetype'),
        url('../assets/fonts/Merriweather/fonts/merriweather-lightitalic-webfont.svg#merriweatherlight_italic') format('svg');
    font-weight: normal;
    font-style: italic;
}

@font-face {
    font-family: 'Merriweather Web';
